<template>
<div>       
    <router-link :to="{path:'/products'}" tag="div" class="go-back">
        <svg width="24" height="24" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd">
            <path d="M2.117 12l7.527 6.235-.644.765-9-7.521 9-7.479.645.764-7.529 6.236h21.884v1h-21.883z"/>
        </svg>
        <span>Torna indietro</span>
    </router-link>
    <div class="product-detail" :product="product">
        <!-- Inserire altre immagini sul lato sinistro -->
        <div class="img-container">
            <img :src="product.images[0].url" alt="product image" />
        </div>
        <div class="info">
            <span>{{ product.name }}</span>
            <span>{{ product.price }} &euro;</span>
            <span v-if="product.quantity == 0" class="unavailable"
                >Attualmente non disponibile
            </span>
            <div class="size-selector">                
                <button id="size-toggler" @click="toggleSizes">
                    <span>{{selectedSize.text}}</span>                    
                    <svg class="arrow-down" width="24" height="24" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd"><path d="M23.245 4l-11.245 14.374-11.219-14.374-.781.619 12 15.381 12-15.391-.755-.609z"/></svg>
                </button>
                <div class="sizes-container" style="display:none;">
                    <div id="sizes-panel">
                        <span @click="italianSizes=true;USSizes=false">Taglie Italiane</span>
                        <span @click="USSizes=true;italianSizes=false">Taglie Americane</span>   
                    </div>    
                    <div v-for="size in sizes" v-bind:key="size.id">                                    
                        <div 
                            class="size" 
                            @click="selectedSize.text=size.it;selectedSize.available=size.available" 
                            v-show="italianSizes">
                                {{size.it}} 
                                <span class="unavailable">{{size.available== false ? 'Esaurito' : ''}}</span>
                        </div>
                        <div
                            class="size" 
                            @click="selectedSize.text=size.us;selectedSize.available=size.available" 
                            v-show="USSizes">
                                {{size.us}} 
                                <span class="unavailable">{{size.available== false ? 'Esaurito' : ''}}</span>
                        </div>   
                    </div>
                </div>
                <p v-show='selectedSize.available==false' style='color:red;margin:15px 0;'>La taglia selezionata non è al momento disponibile!</p>
                <button>
                    Aggiungi al carrello
                </button>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import { sizes } from '../shared/sizes.js'
import { mapGetters, mapActions } from 'vuex'

export default {
    name: 'ProductDetailComponent',    
    props: {
        id: {
            type: Number,
            default: 0
        }
    },
    data() {
        return {  
            product: {},                      
            sizes: [],
            italianSizes: true,
            USSizes: false,
            selectedSize: {
                text:'Scegli una taglia',
                available:undefined
            }
        }
    },
    created () {        
        this.product = this.getProductById(this.id);
        this.availableSizes();
    },
    computed: {
        ...mapGetters(['getProductById']),
    },
    methods: {
        ...mapActions(['updateProductAction', 'addProductAction']),
        toggleSizes() {            
            let sizesContainer = document.querySelector('.sizes-container');
            sizesContainer.style.display = sizesContainer.style.display== 'none' ? 'block' : 'none';               
        },
        availableSizes() {            
            // TODO taglie miste; prodotti non disponibili
            
            let sizesWrapper = sizes.categories.find(cat => cat.name == this.product.category);            
            this.sizes = this.product.gender == 'M' ? sizesWrapper.male : sizesWrapper.female;            
            let availableSizes = this.sizes.filter(size => this.product.sizes.find(pSize => pSize.value == size.it));            
            availableSizes.forEach(size => size.available = true);
        },

    }
}
</script>

