<template>
    <div>
        <h2>Accedi</h2>
        <form id="login-form">
            <hr>
            <label for="email"><b>Email</b></label>
            <input 
                type="text" 
                placeholder="Inserisci l'email"
                name="email" 
                id="email" 
                required 
                v-model="formData.email"          
            />
            <label for="psw"><b>Password</b></label>
            <input 
                type="password" 
                placeholder="Inserisci la password" 
                name="psw" 
                id="psw" 
                required 
                v-model="formData.password"
            /> 
            <button 
                type="submit" 
                class="loginbtn"
                @click="handleClick"
            >
                Login
            </button>
            <hr>
            <div class="go-to-signup">
                <span>Non sei registrato?</span>        
                <router-link to="/sign-up">
                    <span style="color:lightblue;font-weight:bold"> Inizia qui</span>
                </router-link>
            </div>
        </form>
        
    </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex';
    export default {
        name: 'LoginComponent',
        data() {
            return {
                formData: {
                    email: '',
                    password: ''
                }
            }
        },
        computed: {
            ...mapState(['loggedUser'])
        },
        methods: {
            ...mapActions(['updateCurrentUserAction']),
            async handleClick(event) {
                event.preventDefault();  
                this.resetForm();              
                await this.updateCurrentUserAction(this.formData);         
                if(this.loggedUser == undefined) {
                    alert('credenziali errate')
                } else {
                    this.$router.push({ path: '/' })
                }
            },
            resetForm() {
                document.getElementById('login-form').reset();                
            }
        }
    }
</script>

<style scoped>

#login-form {
    width: 40%;
    margin: 0 auto;
    text-align: start;
    padding: 20px 150px;
}

label {
    display: inline-block;
    width: 100%;
}

input[type=text], input[type=password] {
  width: calc(100% - 30px);
  padding: 15px;
  margin: 5px 0 22px 0;
  display: inline-block;
  border: none;
  background: #f1f1f1;
}

input[type=text]:focus, input[type=password]:focus {
  background-color: #ddd;
  outline: none;
}

hr {
  border: 1px solid #f1f1f1;
  margin-bottom: 25px;
}

.loginbtn {
  background-color: #04AA6D;
  color: white;
  padding: 16px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  opacity: 0.9;
  font-size: 18px;
}

.loginbtn:hover {
  opacity: 1;
}

a {
  color: rgb(76, 157, 238);
}

.go-to-signup {    
    text-align: center;
}
</style>