import Vue from 'vue'
import VueRouter from 'vue-router'
import ProductsListComponent from '../views/products-list-component.vue'
import ProductDetailComponent from '../views/product-detail-component.vue'
import LoginComponent from '../views/login-component.vue'
import SignUpComponent from '../views/sign-up-component.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/products'
  },
  {
    path: '/products',
    name: 'ProductsListComponent',
    component: ProductsListComponent
  },
  {
    path: '/products/:id',
    name: 'product-detail',
    component: ProductDetailComponent,
    props: r => ({id: parseInt(r.params.id)})
  },
  {
    path: '/login',
    name: 'login',
    component: LoginComponent
  },
  {
    path: '/sign-up',
    name: 'sign-up',
    component: SignUpComponent
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  /*mode: 'history',*/
  base: process.env.BASE_URL,
  routes
})

export default router
