<template>
    <div>
        <h2>Crea un account</h2>
        <p style="margin-bottom:0;">Inserisci i dati richiesti per creare un account.</p>
        <form id="registration-form">
            <hr>            
            <label for="email"><b>Email</b></label>
            <input type="text" placeholder="Inserisci l'email" name="email" id="email" required v-model="formData.email"/>
            <label for="psw"><b>Password</b></label>
            <input type="password" placeholder="Inserisci la password" name="psw" id="psw" required v-model="formData.password"/> 
            <label for="psw-repeat"><b>Repeat Password</b></label>
            <input type="password" placeholder="Ripeti la password" name="psw-repeat" id="psw-repeat" required v-model="formData.repeatedPass"/>
            <hr>
            <p>Creando un account dichiari di accettare i nostri <a href="#">Terms & Privacy</a>.</p>
            <button type="submit" class="sign-up-btn" @click="handleClick">Registrati</button>
            <hr>            
            <div class="go-to-login">
                <span>Hai già un account?</span>        
                <router-link to="/login">
                    <span style="color:lightblue;font-weight:bold"> Login</span>
                </router-link>
            </div>
        </form>        
    </div>
</template>

<script>
    import { mapActions } from 'vuex';
    export default {
        name: 'SignUpComponent',
        data() {
          return {
            formData: {
              email: '',
              password: '',   
              repeatedPass: ''           
            }
          }
        },
        computed: {
          userFromForm() {            
            return {email: this.formData.email,password:this.formData.password};
          },
          validateData() {
            if(this.formData.password !== this.formData.repeatedPass) return;
            return true;
          }
        },
        methods: {
          ...mapActions(['addUserAction']),
          async handleClick(event) {
            event.preventDefault();
            if(this.validateData){
              const data = this.userFromForm;               
              await this.addUserAction(data);
            } else {
              alert('le password devono essere uguali')
            }
            this.resetForm();     
          },
          resetForm() {
              document.getElementById('registration-form').reset();                
          },
          
        }
    }
</script>

<style scoped>

#registration-form {
    width: 40%;
    margin: 0 auto;
    text-align: start;
    padding: 20px 150px;
}

label {
    display: inline-block;
    width: 100%;
}

input[type=text], input[type=password] {
  width: calc(100% - 30px);
  padding: 15px;
  margin: 5px 0 22px 0;
  display: inline-block;
  border: none;
  background: #f1f1f1;
}

input[type=text]:focus, input[type=password]:focus {
  background-color: #ddd;
  outline: none;
}

hr {
  border: 1px solid #f1f1f1;
  margin-bottom: 25px;
}

.sign-up-btn {
  background-color: #04AA6D;
  color: white;
  padding: 16px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  opacity: 0.9;
  font-size: 18px;
}

.sign-up-btn:hover {
  opacity: 1;
}

a {
  color: rgb(76, 157, 238);
}

.go-to-login {    
    text-align: center;
}

</style>