<template>
<div>    
    <div class="products-list">
        <div class="product" v-for="product in products" v-bind:key="product.id">
            <router-link :to="{name: 'product-detail', params: {id: product.id}}">
                <img :src="product.images[0].url" alt="product image">
                <div class='info'>
                    <span>{{product.name}}</span>
                    <span>{{product.price}} &euro;</span>           
                    <span v-if="product.quantity==0" class="unavailable">Attualmente non disponibile</span>
                </div>            
            </router-link>    
        </div>   
    </div>
</div>
</template>

<script>
import { mapActions, mapState } from 'vuex';

export default {
    name: 'ProductsListComponent',    
    computed: {
        ...mapState(['products'])
    },
    async created() {        
        await this.getProductsAction();
    },
    methods: {
        // ... sta per 'copiami tutti i metodi in mapActions 
        ...mapActions(['getProductsAction']),
    } 
}
</script>

